import Input from "./Input.js";
import BmiRes from "./BmiRes.js";
import { useReducer } from "react";

export default function Bmi() {
  const [inputValues, setInputValues] = useReducer(
    (state, newState) => {
      console.log("state");
      console.log(state);
      console.log("newState");
      console.log(newState);

      return { ...state, ...newState };
    },
    { height: "", weight: "" }
  );

  const handleOnChange = (evt) => {
    const { name, value } = evt.target;
    setInputValues({ [name]: value });
  };

  return (
    <div className="bmi">
      {/* <h3 className="fw-bolder mb-4">BMI </h3> */}
      <p className="text-muted mb-6">
         (Body Mass Index, BMI) 
      </p>
      <div className="container mt-4">
        <div className="row">
          <div className="col-md-6">
            <div>
              <p></p>
              <p>
                BMI =  () / <sup>2</sup> (<sup>2</sup>)
              </p>
            </div>

            <div>
              <Input
                label={""}
                id="height"
                name="height"
                min={0}
                hint={" cm"}
                type={"number"}
                onChange={handleOnChange}
                value={inputValues.height}
                className="form-control"
              />
              <br />
            </div>
            <div>
              <Input
                label={""}
                id="weight"
                name="weight"
                min={0}
                hint={" kg"}
                type={"number"}
                onChange={handleOnChange}
                value={inputValues.weight}
                className="form-control"
              />
              <br />
            </div>
            {/*  */}
            {/* INPUT  */}
            {inputValues.height || inputValues.weight ? (
              <button
                className="btn btn-outline-secondary btn-sm float-end"
                onClick={() => setInputValues({ height: "", weight: "" })}
              >
                
              </button>
            ) : null}
          </div>
          <div className="col-md-6">
            <table className="table table-bordered  text-center">
              <thead className="thead-light">
                <tr>
                  <th style={{ width: "100px" }}></th>
                  <th style={{ width: "150px" }}>
                     (BMI)
                    <br />
                    (kg/m)
                  </th>
                  <th style={{ width: "150px" }}>
                    
                    <br />
                    (cm)
                  </th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td className="bg-info text-white"></td>
                  <td>BMI  18.5</td>
                  <td>-</td>
                </tr>
                <tr>
                  <td className="bg-info text-white"></td>
                  <td>18.5  BMI  24</td>
                  <td>-</td>
                </tr>
                <tr>
                  <td className="bg-info text-white"></td>
                  <td>
                    <div>
                      24  BMI  27
                      <br />
                      27  BMI  30
                      <br />
                      30  BMI  35
                      <br />
                      BMI  35
                    </div>
                  </td>
                  <td>
                     90
                    <br />
                     80
                  </td>
                </tr>
              </tbody>
            </table>
            <p className="fs-6 float-end m-0"></p>
          </div>
        </div>
        <BmiRes weight={inputValues.weight} height={inputValues.height} />
      </div>
    </div>
  );
}
