import { Link, useOutletContext } from "react-router-dom";
import { useState, useEffect, useRef } from "react";
import axios from "axios";
import DeleteModal from "../../components/DeleteModal";
import { Modal } from "bootstrap";

function Cart() {
  const { cartData, getCart } = useOutletContext();
  const [loadingItems, setLoadingItem] = useState([]);

  //  tempProduct 
  //  tempProduct 
  const [tempProduct, setTempProduct] = useState({});

  //  useRef  modal
  const deleteModal = useRef(null);

  // 
  const openDeleteModal = (product) => {
    //   tempProduct
    //  props  ProductModal
    console.log(product);
    setTempProduct(product);
    deleteModal.current.show();
  };

  const closeDeleteModal = () => {
    deleteModal.current.hide();
  };

  const removeCartItem = async (id) => {
    console.log(id);
    try {
      const res = await axios.delete(
        `/v2/api/${process.env.REACT_APP_API_PATH}/cart/${id}`
      );
      console.log(res);
      if (res.data.success) {
        getCart();
        closeDeleteModal();
      }
    } catch (error) {
      console.log(error);
    }
  };

  const updateCartItem = async (item, quantity) => {
    const data = {
      data: {
        product_id: item.product_id,
        qty: quantity,
      },
    };
    setLoadingItem([...loadingItems, item.id]);
    try {
      const res = await axios.put(
        `/v2/api/${process.env.REACT_APP_API_PATH}/cart/${item.id}`,
        data
      );
      console.log(res);
      setLoadingItem(
        loadingItems.filter((loadingObject) => loadingObject !== item.id)
      );
      getCart();
    } catch (error) {
      console.log(error);
    }
  };

  // function formatCurrency(amount) {
  //   // 
  //   if (isNaN(amount)) {
  //     throw new Error("");
  //   }

  //   //  toLocaleString 
  //   return "NT$" + Number(amount).toLocaleString("zh-TW");
  // }

  //  useEffect ()
  useEffect(() => {
    //  modal
    //  deleteModal  useRef  Modal
    deleteModal.current = new Modal("#deleteModal", {
      backdrop: "static",
    });

    // 
    getCart();
  }, []);

  return (
    <div className="container">
      <DeleteModal
        text={tempProduct?.product?.title}
        close={closeDeleteModal}
        handleDelete={removeCartItem}
        id={tempProduct.id}
      />
      <div className="row d-flex justify-content-center">
        <div className="col-md-8 bg-white py-5 full-height">
          {cartData?.carts?.length === 0 ? (
            <>
              <div className="alert alert-secondary text-center py-6">
                
              </div>
              <Link
                to="/products"
                className="btn btn-primary rounded-3 w-100  rounded-0 py-3"
              >
                
              </Link>
            </>
          ) : (
            <>
              <div className="d-flex justify-content-between">
                <h2 className="mt-2"></h2>
              </div>

              {cartData?.carts?.map((item) => {
                return (
                  <div
                    className="d-flex mt-4 bg-light cart-bg-hover rounded-4"
                    key={item.id}
                  >
                    <Link to={`/product/${item.product.id}`}>
                      <img
                        src={item.product.imageUrl}
                        alt=""
                        className="img-fluid object-cover rounded-start-4"
                        style={{
                          width: "250px",
                          height: "150px",
                        }}
                      />
                    </Link>
                    <div className="w-100 p-3 position-relative">
                      <button
                        type="button"
                        className="position-absolute btn"
                        style={{ top: "10px", right: "10px" }}
                        onClick={() => {
                          openDeleteModal(item);
                        }}
                      >
                        <i className="bi bi-trash3 text-danger fs-5"></i>
                      </button>
                      <p className="mb-0 fw-bold">{item.product.title}</p>
                      <p
                        className="mb-1 text-muted"
                        style={{ fontSize: "14px" }}
                      >
                        {item.product.content}
                      </p>
                      <div className="d-flex justify-content-between align-items-center w-100">
                        <div className="input-group w-50 align-items-center">
                          <select
                            name=""
                            className="form-select"
                            id=""
                            value={item.qty}
                            disabled={loadingItems.includes(item.id)}
                            onChange={(e) => {
                              updateCartItem(item, e.target.value * 1);
                            }}
                          >
                            {[...new Array(20)].map((i, num) => {
                              return (
                                <option value={num + 1} key={num}>
                                  {num + 1}
                                </option>
                              );
                            })}
                          </select>
                        </div>
                        <p className="mb-0 ms-auto">NT${item.final_total}</p>
                      </div>
                    </div>
                  </div>
                );
              })}
              <div className="d-flex justify-content-between mt-4">
                <p className="mb-0 h4 fw-bold"></p>
                <p className="mb-0 h4 fw-bold">NT${cartData.final_total}</p>
              </div>
              <Link
                to="/checkout"
                className="btn btn-primary w-100 mt-4 rounded-3 py-3 "
              >
                
              </Link>
            </>
          )}
        </div>
      </div>
    </div>
  );
}

export default Cart;
