import { useContext, useEffect, useState } from "react";
import axios from "axios";
import {
  MessageContext,
  handleSuccessMessage,
  handleErrorMessage,
} from "../store/messageStore";

function ProductModal({ closeProductModal, getProducts, type, tempProduct }) {
  // 
  const [tempData, setTempData] = useState({
    // 
    title: "",
    category: "",
    origin_price: 0,
    price: 0,
    unit: "",
    description: "",
    content: "",
    is_enabled: 1,
    imageUrl: "",
    imagesUrl: ["", "", "", "", ""],
  });

  //  dispatch
  const [, dispatch] = useContext(MessageContext);

  useEffect(() => {
    // 
    if (type === "create") {
      setTempData({
        title: "",
        category: "",
        origin_price: 0,
        price: 0,
        unit: "",
        description: "",
        content: "",
        is_enabled: 1,
        imageUrl: "",
        imagesUrl: ["", "", "", "", ""],
      });
    } else if (type === "edit") {
      setTempData(tempProduct);
    }
  }, [type, tempProduct]);

  //  () 
  const handleChange = (e) => {
    // name input  title ->  api title 
    // value 
    const { name, value } = e.target;
    // matchImages  images 
    const matchImages = name.match("images")?.[0]; // "images"  undefined

    //  input  value 
    // 
    //  name  includes 

    if (["price", "origin_price"].includes(name)) {
      setTempData({
        ...tempData,
        [name]: Number(value),
      });
    } else if (name === "is_enabled") {
      //  checkbox 
      setTempData({
        ...tempData,
        [name]: +e.target.checked, //  false  true  0  1
      });
    } else if (matchImages) {
      const index = name.substring(10) - 1; // input
      setTempData((pre) => ({
        ...pre,
        imagesUrl: pre.imagesUrl.map((item, i) => {
          if (i === index) {
            return value;
          }
          return item;
        }),
      }));
    } else {
      setTempData({
        ...tempData, // 
        [name]: value, //  : 
      });
    }
  };

  //  post 
  const submit = async () => {
    try {
      // 
      let api = `/v2/api/${process.env.REACT_APP_API_PATH}/admin/product`;
      let method = "post";

      //  ( id)
      if (type === "edit") {
        api = `/v2/api/${process.env.REACT_APP_API_PATH}/admin/product/${tempProduct.id}`;
        method = "put";
      }

      // 
      const res = await axios[method](api, {
        data: tempData, //  api  data 
      });
      console.log(res);

      //  messageStore.js  POST_MESSAGE
      handleSuccessMessage(dispatch, res);

      // submit
      // 
      closeProductModal();
      //  props 
      getProducts();
    } catch (error) {
      console.log(error);

      //  messageStore.js  POST_MESSAGE
      handleErrorMessage(dispatch, error);
    }
  };

  // /
  const uploadFile = async (file) => {
    console.log(file);
    if (!file) return;

    // FormData 
    const formData = new FormData();
    // swagger  input  name  file-to-upload file
    formData.append("file-to-upload", file);

    try {
      const res = await axios.post(
        `/v2/api/${process.env.REACT_APP_API_PATH}/admin/upload`,
        formData
      );
      console.log(res);
      if (res.data.success) {
        setTempData({
          ...tempData,
          imageUrl: res.data.imageUrl,
        });
      }
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <div
      className="modal fade"
      tabIndex="-1"
      id="productModal"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div className="modal-dialog modal-xl">
        <div className="modal-content">
          <div className="modal-header">
            <h1 className="modal-title fs-5" id="exampleModalLabel">
              {/* Modal  */}
              {type === "create"
                ? ""
                : ` ${tempProduct.title}`}
            </h1>
            <button
              type="button"
              className="btn-close"
              aria-label="Close"
              onClick={closeProductModal}
            />
          </div>
          <div className="modal-body">
            <div className="row">
              <div className="col-sm-12">
                {/* <pre>{JSON.stringify(tempData)}</pre> */}
                <div className="form-group mb-2">
                  <label className="w-100" htmlFor="title">
                    
                    <input
                      type="text"
                      id="title"
                      name="title"
                      placeholder=""
                      className="form-control"
                      onChange={handleChange}
                      value={tempData.title}
                    />
                  </label>
                </div>
                <div className="row">
                  <div className="form-group mb-2 col-md-6">
                    <label className="w-100" htmlFor="category">
                      
                      <input
                        type="text"
                        id="category"
                        name="category"
                        placeholder=""
                        className="form-control"
                        onChange={handleChange}
                        value={tempData.category}
                      />
                    </label>
                  </div>
                  <div className="form-group mb-2 col-md-6">
                    <label className="w-100" htmlFor="unit">
                      
                      <input
                        type="unit"
                        id="unit"
                        name="unit"
                        placeholder=""
                        className="form-control"
                        onChange={handleChange}
                        value={tempData.unit}
                      />
                    </label>
                  </div>
                </div>
                <div className="row">
                  <div className="form-group mb-2 col-md-6">
                    <label className="w-100" htmlFor="origin_price">
                      
                      <input
                        type="number"
                        id="origin_price"
                        name="origin_price"
                        placeholder=""
                        className="form-control"
                        onChange={handleChange}
                        value={tempData.origin_price}
                      />
                    </label>
                  </div>
                  <div className="form-group mb-2 col-md-6">
                    <label className="w-100" htmlFor="price">
                      
                      <input
                        type="number"
                        id="price"
                        name="price"
                        placeholder=""
                        className="form-control"
                        onChange={handleChange}
                        value={tempData.price}
                      />
                    </label>
                  </div>
                </div>
                <hr />
                <div className="form-group mb-2">
                  <label className="w-100" htmlFor="description">
                    
                    <textarea
                      type="text"
                      id="description"
                      name="description"
                      placeholder=""
                      className="form-control"
                      onChange={handleChange}
                      value={tempData.description}
                    />
                  </label>
                </div>
                <div className="form-group mb-2">
                  <label className="w-100" htmlFor="content">
                    
                    <textarea
                      type="text"
                      id="content"
                      name="content"
                      placeholder=""
                      className="form-control"
                      onChange={handleChange}
                      value={tempData.content}
                    />
                  </label>
                </div>
                <div className="form-group mb-2">
                  <div className="form-check">
                    <label
                      className="w-100 form-check-label"
                      htmlFor="is_enabled"
                    >
                      
                      <input
                        type="checkbox"
                        id="is_enabled"
                        name="is_enabled"
                        placeholder=""
                        className="form-check-input"
                        onChange={handleChange}
                        checked={Boolean(tempData.is_enabled)}
                        // checked={!!tempData.is_enabled} // 
                      />
                    </label>
                  </div>
                </div>
              </div>
              <div className="col-sm-12">
                <div className="form-group mb-2">
                  <label className="w-100 border-top py-4" htmlFor="customFile">
                    <p className="fs-3 fw-bold"></p>
                    <input
                      type="file"
                      id="customFile"
                      className="form-control"
                      onChange={(e) => {
                        uploadFile(e.target.files[0]);
                      }}
                    />
                  </label>
                </div>
                {/*  */}
                {tempData.imageUrl && (
                  <img src={tempData.imageUrl} alt="" className="img-fluid" />
                )}

                <div className="form-group mb-2">
                  <label className="form-label "></label>
                  <div
                    style={{ display: "flex", flexWrap: "wrap" }}
                    className="bg-light"
                  >
                    {[...Array(4)].map((_, index) => (
                      <div
                        key={index + 1}
                        style={{ flex: "1 0 33%", margin: "10px" }}
                      >
                        <input
                          type="text"
                          name={`imagesUrl-${index + 1} `}
                          className={`form-control mb-1`}
                          id={`productImagesUrl${index + 1}`}
                          placeholder={` ${index + 1} `}
                          onChange={handleChange}
                          value={tempData.imagesUrl[index] || ""} //  undefined
                        />
                        {tempData.imagesUrl && tempData.imagesUrl[index] && (
                          <img
                            src={tempData.imagesUrl[index]}
                            alt=""
                            className="img-fluid admin-modal-img"
                          />
                        )}
                      </div>
                    ))}
                  </div>
                </div>

                {/* {JSON.stringify(tempData.imagesUrl)} */}

                {/* <hr />
                {tempData.imagesUrl &&
                  tempData.imagesUrl.map((image, index) => (
                    <img key={index} src={image} alt="" className="img-fluid" />
                  ))} */}
              </div>
            </div>
          </div>
          <div className="modal-footer">
            <button
              type="button"
              className="btn btn-secondary"
              onClick={closeProductModal}
            >
              
            </button>
            <button type="button" className="btn btn-primary" onClick={submit}>
              
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ProductModal;
