import { useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { NavLink } from "react-router-dom";
// import { Link } from "react-router-dom";

function Login() {
  const navigate = useNavigate();

  // 
  const [data, setData] = useState({
    username: "",
    password: "",
  });

  const [loginState, setLoginState] = useState({});

  //   input 
  const handleChange = (e) => {
    //  e.target.name  input  name e.target.value  input  value 
    const { name, value } = e.target;

    //  data 
    setData({
      ...data, // 
      [name]: value, // 
    });
  };

  //  api 
  const submit = async (e) => {
    //  try catch 
    try {
      // post  data
      const res = await axios.post(`/v2/admin/signin`, data);

      //  token
      const { token, expired } = res.data;

      //  token  cookie  (toekn  )
      document.cookie = `reactAccessToken=${token}; expires=${new Date(
        expired
      )};`;

      //  /admin/products
      if (res.data.success) {
        navigate("/admin/products");
      }
    } catch (error) {
      // catch error 
      setLoginState(error.response.data);
    }
  };

  return (
    <div
      className="bg-img"
      style={{ minHeight: "100vh", "--bs-bg-opacity": ".1" }}
    >
      <div className="bg-light ">
        <nav className="navbar container d-flex justify-content-between px-2 px-md-0">
          <NavLink to="/">
            <p className="m-0 fs-1 logo text-black">FIT her.</p>
          </NavLink>
          <NavLink
            to="/"
            className=" btn btn-outline-primary  fw-bolder  border border-primary py-2 px-3 rounded-start-pill rounded-end-pill"
          >
            
          </NavLink>
        </nav>
      </div>

      <div className="full-height container py-5 d-flex justify-content-center align-items-center ">
        <div className="row w-100 w-md-50 ">
          <div className="col p-0">
            <div className="d-flex justify-content-center align-items-center">
              <p className="display-4 fw-bolder text-light"></p>
              <p className="display-4 fw-lighter text-light"></p>
            </div>

            {/*  */}
            <div
              className={`alert alert-danger ${
                loginState.message ? "d-block" : "d-none"
              }`}
              role="alert"
            >
              {loginState.message}
            </div>

            <div className="">
              <label htmlFor="email" className="form-label w-100">
                <p className="text-light m-0">Email</p>
                <input
                  id="email"
                  className="form-control rounded-start-pill rounded-end-pill"
                  name="username"
                  type="email"
                  placeholder=""
                  onChange={handleChange}
                />
              </label>
            </div>
            <div className="">
              <label htmlFor="password" className="form-label w-100">
                <p className="text-light m-0"></p>
                <input
                  type="password"
                  className="form-control rounded-start-pill rounded-end-pill"
                  name="password"
                  id="password"
                  placeholder=""
                  onChange={handleChange}
                />
              </label>
            </div>
            <button
              type="button"
              className="mt-4 btn btn-primary w-100 rounded-start-pill rounded-end-pill"
              onClick={submit}
            >
              <p className="fw-600 m-0"></p>
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Login;
