import { createContext } from "react"; //  createContext  context

// useContext 
export const MessageContext = createContext(); //  context

//  state
export const initState = {
  type: "",
  title: "",
  text: "",
};

// Reducer
// state  action 
export const messageReducer = (state, action) => {
  switch (action.type) {
    // 
    case "POST_MESSAGE":
      return {
        ...action.payload, //  action.payload  state
      };
    // 
    case "CLEAR_MESSAGE":
      return {
        ...initState, //  initState  state
      };
    default:
      return state;
  }
};

// 
export function handleSuccessMessage(dispatch, res) {
  dispatch({
    type: "POST_MESSAGE",
    payload: {
      type: "success",
      title: "",
      text: res.data.message,
    },
  });
  // 3 
  setTimeout(() => {
    dispatch({
      type: "CLEAR_MESSAGE",
    });
  }, 3000);
}

// 
export function handleErrorMessage(dispatch, error) {
  dispatch({
    type: "POST_MESSAGE",
    payload: {
      type: "danger",
      title: "",
      //  join 
      text: Array.isArray(error?.response?.data?.message)
        ? error.response.data.message.join("")
        : error.response.data.message,
    },
  });
  // 3 
  setTimeout(() => {
    dispatch({
      type: "CLEAR_MESSAGE",
    });
  }, 3000);
}

